<template>
    <div class="page-index">
        <van-list
        v-if="data"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        >
            <ul>
                <li v-for="(item,key) in data" :key="key" @click="answersDetailsClick(item.tid)">
                    <div class="left">
                        <p>{{ item.dateline | timeformat()}}</p>
                    </div>
                    <div class="right">
                        <p class="message">{{item.message}}</p>
                        <div class="image-box" v-if="item.attachment == 2 " >
                            <img :src="url"  v-for="(url,key_1) in item.image_list" :key="key_1" @click="handlePreviewImage(item.image_list,key_1 )"/>
                        </div>
                        <div class="label flex justify-space-between">
                            <div>
                                <div class="views">{{item.views}}</div>
                                <div class="replies">{{item.replies}}</div>
                            </div>
                            <div class="money">
                                <p>打赏金额:<span>¥{{item.question_price}}</span></p>
                                <div class="reward_type" v-if="item.reward_type !=0" :class="{'reward_type_fen':['2','3','4'].indexOf(item.reward_type) > -1}" ></div>
                            </div>
                        </div>
                        
                    </div>
                    
                </li>
            </ul>
        </van-list>
    </div>
</template>
<script>
import {formatTimestamp} from '@js/moment'
import { ImagePreview } from 'vant';
export default {
    props:['uid','type'],
    data(){
        return {
            data:'',
            loading:false,
            finished:false,
            pageInfo:{
                page_size:10,
                page_index:0
            }
        }
    },
    created(){
        this.getMyThread(this.uid);
    },
    methods:{
        onLoad(){
            this.getMyThread(this.uid);
        },
        handlePreviewImage(v,key){
            ImagePreview({
                images:v,
                startPosition:key
            })
        },        
        getMyThread(uid){
            this.api.getMyThread({
                token:this.$store.state.userData,
                uid:uid,
                type:2,
                special:0,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                this.loading = false;
                if(res.err_code == 0){
                    this.pageInfo.page_index++;
                    if(!res.data.my_thread_data.length){
                        this.finished = true;
                    }
                    this.data = [...this.data,...res.data.my_thread_data] 
                }
            })
        },
        //回答详情
        answersDetailsClick(id) {
        let url = `${this.$store.state.indexUrl+'answers/answersDetails?id='+id+'&'+this.$store.state.appParams}`,
            json = {url:url,share:'0'};
        if(!this.$store.state.userData){
            window.location.href= "teldapp://login"
        }else {
        this.$router.push({ path: "/answers/answersDetails", query: { id: id } });
            window.location.href= `teldapp://newWebview?params=`+JSON.stringify(json)

        }

        }        
    },
    filters:{
        timeformat(v){
            return formatTimestamp(v*1000,'MM-DD')
        }
    }    
}
</script>
